@import "../mixins/fontLora";
@import "../mixins/button";

$breakpoint-request-form-mobile: 500px;

.form {
  padding-top: 20px;
  padding-bottom: 28px;
  width: 420px;

  &__title {
    @include fontLora(500, 30px);
    line-height: 46px;
    text-align: center;
  }
  &__name {
    margin: 40px auto 0;
  }
  &__phone {
    margin: 20px auto 0;
  }
  &__btn-wrapper {
    margin-top: 40px;
    display: flex;
    justify-content: center;
  }
  &__btn {
    @include button(20px 61.5px, 16px);
  }
  &__chkbox {
    margin: 16px 57px 0;
    text-align: center;
  }

  @media (max-width: $breakpoint-tablet-max) {
    box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 41.7776px 33.4221px rgba(0, 0, 0, 0.0503198), 0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275), 0px 12.5216px 10.0172px rgba(0, 0, 0, 0.035), 0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725), 0px 2.76726px 2.21381px rgba(0, 0, 0, 0.0196802);
  }
  @media (max-width: $breakpoint-request-form-mobile) {
    width: 100%;
  }
}